import {v1} from "uuid";

const ADD_MESSAGE = "DIALOGS/ADD-MESSAGE"


let initialState = {
    dialogs: [
        {
            name: "Pasha",
            id: "1",
            img: "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRvaiDjBhS6CiJvhUPw7FlgdOtMMYoBjq7oDw&usqp=CAU"
        },
        {
            name: "Sasha",
            id: "2",
            img: "data:image/jpeg;base64,"
        },
        {
            name: "Masha",
            id: "3",
            img: "data:image/jpeg;base64,"
        },
        {
            name: "Svetlana",
            id: "4",
            img: "data:image/jpeg;base64,"
        },
        {
            name: "Dasha",
            id: "5",
            img: "data:image/jpeg;base64,"
        },
    ],
    messagesData: [
        {message: "Hi!", id: v1()},
        {message: "Man!", id: v1()},
        {message: "Yo!", id: v1()},
        {message: "Shake your head, shake your as!", id: v1()},
        {message: "it-kamasutra and it-incubator", id: v1()},
        {message: "Yo!", id: v1()},
        {message: "YoYoYo!", id: v1()},
    ]
}

export const DialogsReducer = (state = initialState, action) => {

    switch (action.type) {
        case ADD_MESSAGE :
            let newMessage = {
                message: action.newFormMessage,
                id: v1()
            }
            return {
                ...state,
                newMessageText: "",
                messagesData: [...state.messagesData, newMessage]
            }


        default:
            return state

    }

    // if(action.type === ADD_MESSAGE){
    //     let newMessage =  {
    //         message: state.newMessageText,
    //         id: "8"
    //     }
    //     state.messagesData.push(newMessage)
    //     state.newMessageText = ""
    // }
    // else if(action.type === UPDATE_MESSAGE_TEXT){
    //     state.newMessageText = action.newText
    // }


}
export const addNewMessageActionCreator = (newFormMessage) => {
    return {
        type: "DIALOGS/ADD-MESSAGE", newFormMessage
    }
}
